  justify-content: center;
  align-items: center;
} */

#date{
  border: 1px solid #198754;
  margin-bottom: 10px;
  padding: 10px;
  border-radius: 10px;
  margin: 5px;
}

#date:focus{
  border: 1px solid #198754;
  outline: none;
}

#time{
  border: 1px solid #198754;
  margin-bottom: 10px;
  padding: 10px;
  border-radius: 10px;
  margin: 5px;
}

#time:focus{
  border: 1px solid #198754;
  outline: none;
}

.addRemainderBtn{
  background-color: #5C76E5;
  color: whitesmoke;
  border: none;
  width: 50%;
  padding: 10px;
  margin: 20px;
  border-radius: 10px;
}

#editInput{
  border: 1px solid #198754;
  margin-bottom: 10px;
  padding: 10px;
  border-radius: 10px;
  margin: 5px;
}

#editInput:focus{
  border: 1px solid #198754;
  outline: none;
}

#nameInput{
  border: 1px solid #198754;
  margin-bottom: 10px;
  padding: 10px;
}

#nameInput:focus{
  border: 1px solid #198754;
  outline: none;
}

.editUpdateBtn{
  background-color: #5C76E5;
  color: whitesmoke;
  border: none;
  width: 50%;
  padding: 10px;
  margin: 20px;
  border-radius: 10px;
}

@keyframes dots-7ar3yq {
  20% {
     background-position: 0%   0%, 50%  50%,100%  50%;
  }

  40% {
     background-position: 0% 100%, 50%   0%,100%  50%;
  }

  60% {
     background-position: 0%  50%, 50% 100%,100%   0%;
  }

  80% {
     background-position: 0%  50%, 50%  50%,100% 100%;
  }
}

.headerName{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 10px 20px;
  color: #646469;
}



